$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

.page-template-page-home {
  #homeIntro {
    background-color: $blue;
    height: 85vh;
    background-position: center;
    background-size: cover;
    .homeIntroInner {
      .button.white {
        margin-right: 1rem;
      }
    }
  }
  #homeWelcome {
    .ulp-welcome-logo {
      width: 186px;
      display: block;
      margin: auto;
    }
  }
  #homeFeatureThree {
    .homeFeatureContainer {
      background-color: $blue;
      background-size: cover;
      background-position: center;
      border-radius: 5px;
      color: #fff;
      padding: 2rem;
      .homeFeatureIconContainer {
        border-radius: 50%;
        width: 100px;
        height: 100px;
        margin: 2rem auto 4rem auto;
        overflow: hidden;
        background: rgba(255,255,255,0.2);
        i {
          font-size: 2rem;
        }
      }
      .button.cta {
        color: $blue;
        &:hover {
          color: $yellow;
        }
      }
    }
  }
  #homeNews {
    background: #f2f2f2;
    color: $blue;
    .homeNewsPostContainer {
      margin-bottom: 15px;
      .homeNewsThumbContainer {
        position: relative;
        height: 200px;
        background-size: cover;
        background-position: center;
        border-radius: 5px;
        .homeNewsThumbLink {
          position: absolute;
          top: 0; right: 0; bottom: 0; left: 0;
          z-index: 2;
        }
        .homeNewsThumbDate {
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          background: $yellow;
          color: $white;
          text-align: center;
          font-size: 0.75em;
          padding: 5px;
        }
      }
      h3 {
        a {
          color: $blue;
          &:hover {
            text-decoration: none;
          }
        }
      }
    }
    #homeTestimonials {
      .homeTestimonialsContainer {
        background: #fff;
        border-radius: 5px;
        border: 1px solid #ddd;
        padding: 2rem;
        min-height: 400px;
        .homeTestiSlider {
          width: 100%;
        }
        .homeTestimonialsContainerInner {
          .homeTestimonialsImgWrap {
            width: 100px;
            height: 100px;
            border-radius: 50%;
            border: 2px solid $yellow;
            display: inline-block;
            margin: auto auto 2rem auto;
          }
          img {
            border: 2px solid $white;
            border-radius: 50%;
            overflow: hidden;
          }
          .homeTestAuthorTitle {
            color: $yellow;
          }
        }
      }
    }
  }
  #homeCTA {
    background-color: $blue;
    background-size: cover;
    background-position: center;
    color: $white;
  }
  #homeSponsors {
    h2 span {
      color: $yellow;
    }
    .homeSponsorsContainer {
      width: 850px;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
      img {
        margin-bottom: 2rem;
      }
      h4 {
        margin-top: 2rem;
      }
    }
  }
}